import React, { useState, useEffect } from 'react';
import { Card, Spin, message, Row, Col, Table } from 'antd';
import { Helmet } from 'react-helmet';
import Page from 'components/LayoutComponents/Page';
import { getStudentByCode, searchStudentAccount } from 'services/student';
// import { getQueryParams } from 'utils/urlUtil';
import Profile from './Profile';
import SearchBar from './SearchBar';
import './style.scss';

const StudentResume = () => {
  const [studentCode, setStudentCode] = useState('');
  const [hide, setHide] = useState(false);
  const [student, setStudent] = useState({});
  const [paramsSearch, setParamsSearch] = useState({});
  const [pagination, setPagination] = useState({});
  const [loading, setLoading] = useState(false);
  const [dataTable, setDataTable] = useState([]);

  const columns = [
    {
      title: 'Mã sinh viên',
      dataIndex: 'code',
      key: 'code',
    },
    {
      title: 'Họ và tên',
      dataIndex: 'fullName',
      key: 'fullName',
    },
    {
      title: 'Ngày sinh',
      dataIndex: 'dob',
      key: 'dob',
    },
    {
      title: 'Giới tính',
      dataIndex: 'genderText',
      key: 'genderText',
    },
    {
      title: 'Tên lớp',
      dataIndex: 'className',
      key: 'className',
    },
  ]

  const getStudentInformation = async () => {
    // if (!studentCode || studentCode === '') {
    //   setHide(true);
    //   return;
    // }
    setLoading(true);
    try {
      const res = await getStudentByCode(studentCode);
      const newStudent = { ...res.data };
      setStudent(newStudent);
      document.getElementsByClassName('profile')[0].scrollIntoView();
      // setHide(false);
    } catch (err) {
      message.error(`Không tìm thấy sinh viên với mã ${studentCode}`);
      // setHide(true);
    }
    setLoading(false);
  };

  const searchStudentAccountData = async () => {
    setLoading(true);
    try {
      const res = await searchStudentAccount({ ...paramsSearch, ...pagination });
      const tempPagination = { ...pagination };
      tempPagination.total = res.data.totalResults;
      setPagination(tempPagination);
      const tempData = [...res.data.items];
      setDataTable(tempData);
      setHide(false);
    } catch (err) {
      message.error(`Không tìm thấy sinh viên`);
      setHide(true);
    }
    setLoading(false);
  };


  const handleTableChange = paginationTable => {
    const pager = { ...pagination };
    pager.page = paginationTable.current;
    setPagination(pager);
  };

  useEffect(() => {
    if (pagination.page !== undefined) {
      searchStudentAccountData();
    }
  }, [pagination.page]);


  useEffect(() => {
    if (studentCode !== "") {
      getStudentInformation();
    }
  }, [studentCode]);

  useEffect(() => {
    if (!!paramsSearch.studentCode || !!paramsSearch.fullname) {
      searchStudentAccountData()
    } else {
      setHide(true);
    }
  }, [paramsSearch]);

  return (
    <Card title="Lý lịch sinh viên" id="student-resume">
      <Helmet title="Lý lịch sinh viên" />

      <SearchBar setParamsSearch={setParamsSearch} setStudentCode={setStudentCode} />

      <Spin spinning={loading}>
        <div id="profile" style={!hide ? {} : { display: 'none' }}>
          <Row>
            <Col span={24}>
              <Table
                columns={columns}
                rowKey="code"
                dataSource={dataTable}
                pagination={pagination}
                onChange={handleTableChange}
                onRow={(record) => {
                  return {
                    onClick: () => setStudentCode(record.code), // click row
                  };
                }}
              />
            </Col>
          </Row>
          {student.id ?
            <Profile student={student} />
            :
            null
          }

        </div>
      </Spin>
    </Card>
  );
};

export default props => (
  <Page {...props}>
    <StudentResume />
  </Page>
);



