import React from 'react';
import { Tabs } from 'antd';
import InfoAddress from './InfoAddress';
import Avatar from './Avatar';
import TrainingScore from './TrainingScore';
import HistoricalReceipt from './HistoricalReceipt';
import Family from './Family';
import InfoBasic from './InfoBasic';
import InfoPersonal from './InfoPersonal';
import InfoEnrollment from './InfoEnrollment';
import InfoBackground from './InfoBackground';
import './style.scss';

const { TabPane } = Tabs;

const ProfileApp = props => {
  const { student } = props;

  return (
    <div className="profile">
      <div className="row">
        <div style={{ paddingRight: 5 }} className="col-xl-4">
          <div>
            <div className="card profile__header">
              <div className="profile-student__header-card">
                <div className="card-body text-center">
                  <Avatar imageId={student.avatarFileId} />
                  <br />
                </div>
              </div>
              <div className="profile__header-card">
                <div className="card-body">
                  <InfoBasic student={student} />
                </div>
              </div>
            </div>
            <div className="card">
              <div className="profile__actions">
                <div className="card-body">
                  <InfoPersonal student={student} />
                </div>
              </div>
            </div>
            <div className="card">
              <div className="card-body">
                <h5 className="mb-3 text-black">
                  <strong>Thông tin tuyển sinh</strong>
                </h5>
                <div className="profile__actions">
                  <InfoEnrollment student={student} />
                </div>
              </div>
            </div>
          </div>
        </div>
        <div style={{ paddingLeft: 5 }} className="col-xl-8">
          <div className="card">
            <div className="card-body">
              <Tabs defaultActiveKey="1">
                <TabPane
                  tab={
                    <span>
                      <i className="icmn-info" /> Thông tin
                    </span>
                  }
                  key="1"
                  disabled={Object.keys(student).length === 0}
                >
                  <div className="card">
                    <div className="card-body">
                      <h5 className="mb-3 text-black">
                        <strong>Thông tin lý lịch</strong>
                      </h5>
                      <div className="profile__actions">
                        <InfoBackground student={student} />
                      </div>
                    </div>
                  </div>
                  <div className="card">
                    <div className="card-body">
                      <h5 className="mb-3 text-black">
                        <strong>Thông tin nơi chốn</strong>
                      </h5>
                      <div className="profile__actions">
                        <InfoAddress student={student} />
                      </div>
                    </div>
                  </div>
                  <hr />
                </TabPane>
                <TabPane
                  tab={
                    <span>
                      <i className="icmn-address-book" /> Thông tin gia đình
                    </span>
                  }
                  disabled={Object.keys(student).length === 0}
                  key="2"
                >
                  <Family student={student} />
                </TabPane>
                <TabPane
                  disabled={Object.keys(student).length === 0}
                  tab={
                    <span>
                      <i className="icmn-pie-chart" /> Điểm rèn luyện
                    </span>
                  }
                  key="3"
                >
                  <TrainingScore student={student} />
                </TabPane>
                <TabPane
                  disabled={Object.keys(student).length === 0}
                  tab={
                    <span>
                      <i className="icmn-history" /> Lịch sử đóng học phí
                    </span>
                  }
                  key="4"
                >
                  <HistoricalReceipt student={student} />
                </TabPane>
                {/* <TabPane
                    disabled
                    tab={
                      <span>
                        <i className="icmn-address-book" />
                        Điểm học tập
                      </span>
                    }
                    key="5"
                  >
                    <StudyScore />
                  </TabPane> */}
              </Tabs>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProfileApp;



