import React, { Fragment } from 'react';
import IWL from '../component/InformationWithLabel';

const InfoEnrollment = props => {
  const { student } = props;

  return (
    <Fragment>
      <IWL label="Ngoại ngữ" content={student.foreignLanguage} />
      <IWL label="Khu vực" content={student ? student.area : ''} />
      <IWL label="Số báo danh" content={student ? student.code : ''} />
      <IWL label="Điểm tổng cộng" content={student ? student.totalGrade : ''} />
    </Fragment>
  );
};

export default InfoEnrollment;



