import React from 'react';
import { Row, Col } from 'antd';
import { TableData } from 'components/common';
import { getFamily } from 'services/student';

const getColumns = () => {
  const columns = [
    {
      title: 'Họ và tên',
      dataIndex: 'fullname',
      key: 'fullname',
    },
    {
      title: 'Ngày sinh',
      dataIndex: 'dob',
      key: 'dob',
    },
    {
      title: 'Địa chỉ',
      dataIndex: 'homeAddress',
      key: 'homeAddress',
    },
    {
      title: 'Công việc',
      dataIndex: 'career',
      key: 'career',
    },
    {
      title: 'Mối quan hệ',
      dataIndex: 'relationshipTypeLabel',
      key: 'relationshipTypeLabel',
    },
    {
      title: 'SĐT',
      dataIndex: 'phoneNo',
      key: 'phoneNo',
    },
  ];
  return columns;
};

const Family = props => {
  return (
    <Row>
      <Col span={24}>
        <TableData
          reload={props.reload}
          paramSearch={{ studentId: props.student.id }}
          service={getFamily}
          columns={getColumns()}
          rowKey="id"
        />
      </Col>
    </Row>
  );
};

export default Family;



