import React from 'react';
import { Col, Form } from 'antd';
// import { SelectAjax } from 'components/common';
// import { getListTown } from 'services/town';
// import { getListNation } from 'services/nation';
// import { getDistricts } from 'services/district';
// import { getComeFrom } from 'services/comeFrom';
import IWL from '../component/InformationWithLabel';

const InfoAddress = props => {
  const { student } = props;

  return (
    <Form className="form-inline-label" layout="vertical">
      <Col span={12}>
        <IWL label="Quê quán" content={student.homeVillage} />
      </Col>
      <Col span={12}>
        <IWL label="Quốc tịch" content={student.nation} />
      </Col>
      <Col span={12}>
        <IWL label="TP xuất thân" content={student.comeFrom} />
      </Col>
      <Col span={12}>
        <IWL label="ĐC thường trú" content={student.permanentAddress} />
      </Col>
      <Col span={12}>
        <IWL label="Tỉnh/Tp" content={student.town} />
      </Col>
      <Col span={12}>
        <IWL label="Quận/Huyện" content={student.district} />
      </Col>
      <Col span={12}>
        <IWL label="Xã/Phường" content={student.ward} />
      </Col>
      <Col span={12}>
        <IWL label="Địa chỉ báo tin" content={student.emergencyAddress} />
      </Col>
      <Col span={12}>
        <IWL label="Nơi ở hiện nay" content={student.liveAddress} />
      </Col>
    </Form>
  );
};

export default InfoAddress;



