import React from 'react';
import { Col, Form } from 'antd';
// import { SelectAjax } from 'components/common';
// import { getPolicyObjects } from 'services/policyObject';
// import { getEthnicity } from 'services/ethnicity';
// import { getSubsidizeObjects } from 'services/subsidizeObject';
// import { getPrioritizeObjects } from 'services/prioritizeObject';
import moment from 'moment';
import IWL from '../component/InformationWithLabel';

const formatDate = date => {
  if (!date) return '';
  return moment(date).format('YYYY-MM-DD');
};

const InfoBackground = props => {
  const { student } = props;

  return (
    <Form className="form-inline-label" layout="vertical">
      <Col span={12}>
        <IWL label="CMND" content={student.idCard} />
      </Col>
      <Col span={12}>
        <IWL label="Nơi cấp" content={student.placeIssue} />
      </Col>
      <Col span={12}>
        <IWL label="Ngày cấp" content={formatDate(student.dateIssue)} />
      </Col>
      <Col span={12}>
        <IWL label="Dân tộc" content={student.ethnicity} />
      </Col>
      <Col span={12}>
        <IWL label="Tôn giáo" content={student.religion} />
      </Col>
      <Col span={12}>
        <IWL label="Đoàn" content={formatDate(student.unionDate)} />
      </Col>
      <Col span={12}>
        <IWL label="Đảng" content={formatDate(student.politicalDate)} />
      </Col>
      <Col span={12}>
        <IWL label="ĐTCS" content={student.policyObject} />
      </Col>
      <Col span={12}>
        <IWL label="ĐT TC" content={student.subsidizeObject} />
      </Col>
      <Col span={12}>
        <IWL label="ĐT nhà" content={student.homePhone} />
      </Col>
      <Col span={12}>
        <IWL label="Nhóm đối" content={student.prioritizeObject} />
      </Col>
    </Form>
  );
};

export default InfoBackground;



