import React, { Fragment } from 'react';
import IWL from '../component/InformationWithLabel';

const InfoBasic = props => {
  const { student } = props;

  return (
    <Fragment>
      <IWL label="Họ" content={student.firstname} />
      <IWL label="Tên" content={student.lastname} />
      <IWL label="MSSV" content={student.code} />
    </Fragment>
  );
};

export default InfoBasic;



