import React from 'react';
import { hostAPI } from 'utils';
import EmptyAvt from '../../../assets/no-avatar.jpg';
import './style-avatar.scss';

const getImageUrl = imageId => {
  return `${hostAPI}/api/filestorage/Storage/${imageId}/Download`;
};

const Avatar = props => {
  const { imageId } = props;
  return (
    <div className="avatar-profile">
      <img src={imageId ? getImageUrl(imageId) : EmptyAvt} alt="empty" />
    </div>
  );
};

export default Avatar;



