import React, { Fragment, useEffect } from 'react';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import { Form, Input, Row, Col } from 'antd';
import { BtnSearch } from 'components/common/button';

const formItemLayout = {
  labelCol: {
    xs: { span: 24 },
    sm: { span: 7 },
  },
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 17 },
  },
};

const SearchBar = props => {
  const { form, setParamsSearch } = props;
  const { getFieldDecorator } = form;

  useEffect(() => {
    const { studentCode, fullname } = getQueryParams();
    form.setFieldsValue({ studentCode, fullname });
    if(studentCode !== undefined || fullname !== undefined) {
      setParamsSearch(getQueryParams());
    }
  }, []);

  const handleSearch = e => {
    e.preventDefault();
    form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams(values);
      setParamsSearch(values);
    });
  };

  return (
    <Fragment>
      <Form className="form-inline-label" layout="vertical" onSubmit={handleSearch}>
        <Row gutter={24}>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Mã sinh viên">
              {getFieldDecorator('studentCode', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Họ Tên">
              {getFieldDecorator('fullname', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
            </div>
          </Col>
        </Row>
      </Form>
    </Fragment>
  );
};

export default Form.create()(SearchBar);



