import React, { Fragment } from 'react';
import IWL from '../component/InformationWithLabel';

const InfoPersonal = props => {
  const { student } = props;

  return (
    <Fragment>
      <IWL label="Ngày sinh" content={student.dob} />
      <IWL label="Giới tính" content={student.genderText} />
      <IWL label="ĐT cá nhân" content={student.phone} />
      <IWL label="Email" content={student.email} />
    </Fragment>
  );
};

export default InfoPersonal;



